<template>
    <v-dialog v-model="dialog_carrera" fullscreen transition="scroll-y-reverse-transition">
        <template v-slot:activator="{ on }">
            <v-btn v-on="on" rounded block color="primary" class="font-weight-light title">{{
                selectedText || "CARRERA"
            }}</v-btn>
        </template>

        <!-- CARD/MODAL -->
        <v-card>
            <v-toolbar dark color="primary">
                <v-btn icon dark @click="dialog_carrera = false">
                    <v-icon>mdi-close</v-icon>
                </v-btn>
                <v-toolbar-title>CARRERAS</v-toolbar-title>
            </v-toolbar>

            <!-- CONTENIDO -->
            <div v-for="oferta in carreras" v-bind:key="oferta.facultad">
                <v-list subheader expand tile shaped>
                    <v-subheader>{{ oferta.facultad }}</v-subheader>
                    <!-- <v-list-item-group no-action> -->
                    <v-list-item
                        v-for="carrera in oferta.carreras"
                        :key="carrera.carrera"
                        @click="carreraSelect(carrera.carrera)"
                    >
                        <v-list-item-content>
                            <v-list-item-title>{{ carrera.carrera }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                    <!-- </v-list-item-group> -->
                </v-list>

                <v-divider></v-divider>
            </div>
        </v-card>
    </v-dialog>
</template>

<script>
export default {
    data: () => ({
        dialog_carrera: false,
        selectedText: "",
        carreras: [
            {
                facultad: "Facultad de Ciencias Económicas y Sociales",
                carreras: [
                    { carrera: "Administración de Empresas" },
                    { carrera: "Contaduría" },
                    { carrera: "Economía" },
                    { carrera: "Sociología" },
                    { carrera: "Relaciones industriales" },
                ],
            },
            { facultad: "Facultad de Derecho", carreras: [{ carrera: "Derecho" }] },
            {
                facultad: "Facultad de Ingeniería",
                carreras: [
                    { carrera: "Ing. Civil" },
                    { carrera: "Ing. Industrial" },
                    { carrera: "Ing. Informática" },
                    { carrera: "Ing. en Telecomunicaciones" },
                ],
            },
            {
                facultad: "Facultad de Humanidades y Educación",
                carreras: [
                    { carrera: "Comunicación social" },
                    { carrera: "Comunicación social Nuevo Pensum" },
                    { carrera: "Filosofía" },
                    { carrera: "Letras" },
                    { carrera: "Psicología" },
                    { carrera: "Psicología Nuevo Pensum" },
                    { carrera: "Educación tronco comun" },
                    { carrera: "Educación preslied" },
                    { carrera: "Educación mención Biología  y Química" },
                    { carrera: "Educación mención ciencias Pedagógicas " },
                    { carrera: "Educación mención ciencias sociales" },
                    { carrera: "Educación mención Física  y Matemáticas" },
                    { carrera: "Educación mención integral" },
                    { carrera: "Educación mención filosofia" },
                    { carrera: "Educación mención preescolar" },
                ],
            },
        ],
    }),
    methods: {
        carreraSelect(nombreCarrera) {
            console.log("CARRERA: ", nombreCarrera);
            this.selectedText =
                nombreCarrera.length <= 19
                    ? nombreCarrera.toUpperCase()
                    : nombreCarrera.toUpperCase().substr(0, 19) + "..";

            this.$gtag.event("CarreraSelect", {
                event_category: `HplusInteraccion`,
                event_label: nombreCarrera,
            });

            this.$emit("carreraSeleccionada", nombreCarrera);
            this.dialog_carrera = false;
        },
    },
};
</script>
