import Vue from "vue";
import App from "./App.vue";
import vuetify from "./plugins/vuetify";
import "@babel/polyfill";
import VueGtag from "vue-gtag";

Vue.config.productionTip = false;

Vue.use(VueGtag, {
    config: { id: "UA-33542195-4" },
});

new Vue({
    vuetify,
    render: h => h(App),
}).$mount("#app");
