<template>
    <v-app>
        <top-brand></top-brand>

        <v-overlay id="loader" :value="loader_materias" class="text-center">
            <v-progress-circular indeterminate size="64"></v-progress-circular><br />
            <p class="mt-3">CARGANDO MATERIAS</p>
        </v-overlay>

        <v-content>
            <v-col>
                <p class="text-center accent--text text--lighten-1 mb-0 mt-2">
                    ¡Comienza seleccionando tu carrera!
                </p>

                <!-- CARRERA -->
                <v-row justify="center">
                    <v-col sm="12" md="6">
                        <carrera-btn v-on:carreraSeleccionada="cargarMaterias"></carrera-btn>
                    </v-col>
                </v-row>

                <!-- MATERIAS -->
                <v-row justify="center">
                    <v-col sm="12" md="6">
                        <materias-btn
                            :materias="materias"
                            :open="open_materias"
                            :matInscriptas="matInscriptasOriginal"
                            v-on:materiaSelecionada="toggleMateria"
                        ></materias-btn>
                    </v-col>
                </v-row>

                <guardado-btn
                    v-on:dataUltimo="recuperarSecciones"
                    v-show="matInscriptasOriginal.length > 0"
                    :materiasInscriptas="matInscriptasOriginal"
                ></guardado-btn>

                <!-- SECCIONES -->
                <secciones
                    :matInscriptas="matInscriptasOriginal"
                    v-on:guardarSeccion="agregarSeccion"
                    v-on:eliminarSeccion="nulearSeccion"
                    v-on:eliminarMateria="eliminarMateria"
                ></secciones>

                <br />
                <v-row justify="center">
                    <v-col sm="12" md="6">
                        <v-btn
                            rounded
                            block
                            color="primary"
                            class="font-weight-light title"
                            :disabled="matInscriptasOriginal.length == 0"
                            @click="run"
                            >GENERAR</v-btn
                        >
                    </v-col>
                </v-row>
                <br />
                <!-- MSJ No combi -->
                <v-alert dense border="left" type="warning" v-model="showAlert" dismissible>
                    ¡No se encontraron combinaciones posibles!
                    <v-icon>mdi-emoticon-frown-outline</v-icon>
                </v-alert>

                <!-- HORARIOS -->
                <v-row justify="center">
                    <v-col>
                        <div class="horarios" v-for="(horario, i) in horariosGen" :key="i">
                            <div>
                                <h2>Horario {{ i + 1 }}</h2>
                            </div>
                            <v-row>
                                <v-col cols="6">
                                    <v-btn
                                        @click="savePDF(i, 'image')"
                                        class="btn-action"
                                        block
                                        outlined
                                        >DESCARGAR IMAGEN</v-btn
                                    ></v-col
                                >
                                <v-col cols="6">
                                    <v-btn
                                        @click="savePDF(i, 'pdf')"
                                        class="btn-action"
                                        block
                                        outlined
                                        >DESCARGAR PDF</v-btn
                                    ></v-col
                                >
                            </v-row>
                            <v-simple-table dense style="overflow: visible">
                                <template v-slot:default>
                                    <div :id="`hor-${i}`">
                                        <thead>
                                            <tr>
                                                <th>Hora</th>
                                                <th>Lunes</th>
                                                <th>Martes</th>
                                                <th>Miércoles</th>
                                                <th>Jueves</th>
                                                <th>Viernes</th>
                                                <th>Sábado</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="h in 15" :key="`hor-${h - 1}`">
                                                <td>{{ etiquetasHoras[h - 1] }}</td>
                                                <td
                                                    v-if="horario[0].hasOwnProperty([h + 7])"
                                                    :style="{
                                                        backgroundColor:
                                                            matInscriptasOriginal[
                                                                horario[0][h + 7][0]
                                                            ].color,
                                                    }"
                                                    class="white--text"
                                                >
                                                    {{
                                                        matInscriptasOriginal[horario[0][h + 7][0]]
                                                            .materia
                                                    }}
                                                    -
                                                    {{
                                                        matInscriptasOriginal[horario[0][h + 7][0]]
                                                            .nrc[horario[0][h + 7][1]]
                                                    }}
                                                </td>
                                                <td v-else></td>

                                                <td
                                                    v-if="horario[1].hasOwnProperty([h + 7])"
                                                    :style="{
                                                        backgroundColor:
                                                            matInscriptasOriginal[
                                                                horario[1][h + 7][0]
                                                            ].color,
                                                    }"
                                                    class="white--text"
                                                >
                                                    {{
                                                        matInscriptasOriginal[horario[1][h + 7][0]]
                                                            .materia
                                                    }}
                                                    -
                                                    {{
                                                        matInscriptasOriginal[horario[1][h + 7][0]]
                                                            .nrc[horario[1][h + 7][1]]
                                                    }}
                                                </td>
                                                <td v-else></td>

                                                <td
                                                    v-if="horario[2].hasOwnProperty([h + 7])"
                                                    :style="{
                                                        backgroundColor:
                                                            matInscriptasOriginal[
                                                                horario[2][h + 7][0]
                                                            ].color,
                                                    }"
                                                    class="white--text"
                                                >
                                                    {{
                                                        matInscriptasOriginal[horario[2][h + 7][0]]
                                                            .materia
                                                    }}
                                                    -
                                                    {{
                                                        matInscriptasOriginal[horario[2][h + 7][0]]
                                                            .nrc[horario[2][h + 7][1]]
                                                    }}
                                                </td>
                                                <td v-else></td>

                                                <td
                                                    v-if="horario[3].hasOwnProperty([h + 7])"
                                                    :style="{
                                                        backgroundColor:
                                                            matInscriptasOriginal[
                                                                horario[3][h + 7][0]
                                                            ].color,
                                                    }"
                                                    class="white--text"
                                                >
                                                    {{
                                                        matInscriptasOriginal[horario[3][h + 7][0]]
                                                            .materia
                                                    }}
                                                    -
                                                    {{
                                                        matInscriptasOriginal[horario[3][h + 7][0]]
                                                            .nrc[horario[3][h + 7][1]]
                                                    }}
                                                </td>
                                                <td v-else></td>

                                                <td
                                                    v-if="horario[4].hasOwnProperty([h + 7])"
                                                    :style="{
                                                        backgroundColor:
                                                            matInscriptasOriginal[
                                                                horario[4][h + 7][0]
                                                            ].color,
                                                    }"
                                                    class="white--text"
                                                >
                                                    {{
                                                        matInscriptasOriginal[horario[4][h + 7][0]]
                                                            .materia
                                                    }}
                                                    -
                                                    {{
                                                        matInscriptasOriginal[horario[4][h + 7][0]]
                                                            .nrc[horario[4][h + 7][1]]
                                                    }}
                                                </td>
                                                <td v-else></td>

                                                <td
                                                    v-if="horario[5].hasOwnProperty([h + 7])"
                                                    :style="{
                                                        backgroundColor:
                                                            matInscriptasOriginal[
                                                                horario[5][h + 7][0]
                                                            ].color,
                                                    }"
                                                    class="white--text"
                                                >
                                                    {{
                                                        matInscriptasOriginal[horario[5][h + 7][0]]
                                                            .materia
                                                    }}
                                                    -
                                                    {{
                                                        matInscriptasOriginal[horario[5][h + 7][0]]
                                                            .nrc[horario[5][h + 7][1]]
                                                    }}
                                                </td>
                                                <td v-else></td>
                                            </tr>
                                        </tbody>
                                    </div>
                                </template>
                            </v-simple-table>
                            <br />
                        </div>
                    </v-col>
                </v-row>

                <!-- MSJ Generando... -->
                <v-snackbar bottom v-model="msjGen" :timeout="0">
                    <div>
                        <v-progress-circular
                            indeterminate
                            size="16"
                            width="3"
                        ></v-progress-circular>
                        <span class="pl-2"> Generando...</span>
                    </div>
                </v-snackbar>
            </v-col>
        </v-content>
    </v-app>
</template>

<script>
import TopBrand from "./components/TopBrand";
import CarreraBtn from "./components/CarreraBtn";
import MateriasBtn from "./components/MateriasBtn";
import Secciones from "./components/Secciones";
import GuardadoBtn from "./components/GuardadoBtn";

import * as jsPDF from "jspdf";
import html2canvas from "html2canvas";

export default {
    name: "App",

    components: {
        TopBrand,
        CarreraBtn,
        MateriasBtn,
        Secciones,
        GuardadoBtn,
    },

    data: () => ({
        loader_materias: false,
        open_materias: false,
        anyPublicado: false,
        showAlert: false,
        msjGen: false,
        matInscriptasOriginal: [],
        matInscriptas: [],
        materias: [],
        horariosGen: [],
        etiquetasHoras: [
            "7:00 - 7:50am",
            "8:00 - 8:50am",
            "9:00 - 9:50am",
            "10:00 - 10:50am",
            "11:00 - 11:50am",
            "12:00 - 12:50pm",
            "1:00 - 1:50pm",
            "2:00 - 2:50pm",
            "3:00 - 3:50pm",
            "4:00 - 4:50pm",
            "5:00 - 5:50pm",
            "6:00 - 6:50pm",
            "7:00 - 7:50pm",
            "8:00 - 8:50pm",
            "9:00 - 9:50pm",
        ],
    }),

    methods: {
        getUrlCarrera(carrera) {
            let url = "https://madot10.github.io/UCalculadora/js/carreras/";
            switch (carrera) {
                case "Administración de Empresas":
                    return url + "Administración-de-Empresas-1.js";
                case "Contaduría":
                    return url + "contaduria-2017.js";
                case "Economía":
                    return url + "economia-2017.js";
                case "Sociología":
                    return url + "sociologia-2017.js";
                case "Relaciones industriales":
                    return url + "relaciones-industriales-2017.js";
                case "Derecho":
                    return url + "derecho-2017.js";
                case "Ing. Civil":
                    return url + "ingenieria-civil-2017.js";
                case "Ing. Industrial":
                    return url + "ingenieria-industrial-2017.js";
                case "Ing. Informática":
                    return url + "ingenieria-informatica-2017.js";
                case "Ing. en Telecomunicaciones":
                    return url + "ingenieria-en-telecomunicaciones-2017.js";
                case "Comunicación social":
                    return url + "comunicacion-social-2017.js";
                case "Comunicación social Nuevo Pensum":
                    return url + "comunicacion-social-nuevoPensum.js";
                case "Filosofía":
                    return url + "filosofia-2018.js";
                case "Letras":
                    return url + "letras-2018.js";
                case "Psicología":
                    return url + "psicologia-2018.js";
                case "Psicología Nuevo Pensum":
                    return url + "psicologiaNuevoPensum.js";
                case "Educación tronco comun":
                    return url + "educacion-tronco-comun-2017.js";
                case "Educación preslied":
                    return url + "educacion-preslied-2017.js";
                case "Educación mención Biología y Química":
                    return url + "educacion-mencion-biologia-y-quimica-2017.js";
                case "Educación mención ciencias Pedagógicas":
                    return url + "educacion-mencion-ciencias-pedagogicas-2017.js";
                case "Educación mención ciencias sociales":
                    return url + "educacion-mencion-ciencias-sociales-2017.js";
                case "Educación mención Física y Matemáticas":
                    return url + "educacion-mencion-fisica-y-matematicas-2017.js";
                case "Educación mención integral":
                    return url + "educacion-mencion-integral-2017.js";
                case "Educación mención filosofia":
                    return url + "educacion-mencion-filosofia-2017.js";
                case "Educación mención preescolar":
                    return url + "educacion-mencion-preescolar-2017.js";

                default:
                    return null;
            }
        },
        cargarMaterias(carrera) {
            this.loader_materias = true;
            //console.log(this.getUrlCarrera(carrera));

            fetch(this.getUrlCarrera(carrera))
                .then(response => response.text())
                .then(data => {
                    //console.log(data);
                    this.materias = eval(data.substr(data.indexOf("=") + 1).replace(";", ""));
                    this.materias = this.insertMatPracticasOtros(this.materias); //Lab y otros
                    this.getMinors().then(min => {
                        //console.warn("Minor ", min);
                        this.materias = this.materias.concat(min);

                        this.loader_materias = false;
                        this.matInscriptasOriginal = []; //Reiniciamos materias inscriptas
                        this.matInscriptas = [];
                        this.open_materias = true;
                    });
                })
                .catch(error => {
                    console.error(error);
                });
        },
        getMinors() {
            let urlMinor = "https://madot10.github.io/UCalculadora/js/carreras/minors.js";
            let matMinors = [];

            return new Promise((resolve, reject) => {
                matMinors = JSON.parse(sessionStorage.getItem("minors"));
                if (matMinors) {
                    //Lo tenemos guardado
                    //console.warn("IN MEMORY");
                    resolve(matMinors);
                } else {
                    //No esta guardado
                    //console.warn("DOWNLOADING");
                    fetch(urlMinor)
                        .then(response => response.text())
                        .then(data => {
                            matMinors = eval(data.substr(data.indexOf("=") + 1).replace(";", ""));
                            sessionStorage.setItem("minors", JSON.stringify(matMinors));
                            resolve(matMinors);
                        })
                        .catch(error => {
                            console.error(error);
                            reject(null);
                        });
                }
            });
        },
        insertMatPracticasOtros(materias) {
            for (let i = 5; i > 0; i--) {
                let prac = {};
                prac.Semestre = "PRACTICAS/LABORATORIOS";
                prac.Asignatura = `PRACTICA/LABORATORIO ${i}`;
                prac.UC = 0;
                prac.Tax = "TA-9";

                materias.unshift(prac);
            }

            for (let i = 5; i > 0; i--) {
                let prac = {};
                prac.Semestre = "OTROS";
                prac.Asignatura = `OTRO ${i}`;
                prac.UC = 0;
                prac.Tax = "TA-9";

                materias.unshift(prac);
            }

            return materias;
        },
        toggleMateria(id) {
            //Toggle materia para generacion
            if (this.matInscriptasOriginal[id]) {
                //Si esta creado => ELIMINAR
                this.$set(this.matInscriptasOriginal, id, undefined);
            } else {
                //Si no esta creado => CREAR
                let data = {
                    materia: this.materias[id].Asignatura,
                    id: id,
                    color: "",
                    nrc: [],
                    secciones: [],
                };
                this.$set(this.matInscriptasOriginal, id, data);
                //console.log("Materia agregada ID: ", id);
            }
        },
        agregarSeccion(data) {
            if (data.color) this.matInscriptasOriginal[data.idMat].color = data.color;
            this.matInscriptasOriginal[data.idMat].nrc.push(data.nrc);
            this.matInscriptasOriginal[data.idMat].secciones.push(data.horario);
            console.log("Seccion agregada!");
        },
        nulearSeccion(idSec, idMat) {
            console.log("Eliminando seccion: ", idSec, idMat);
            this.matInscriptasOriginal[idMat].nrc[idSec] = null;
            this.$set(this.matInscriptasOriginal[idMat].secciones, idSec, null);
        },
        eliminarMateria(idMat) {
            this.$set(this.matInscriptasOriginal, idMat, undefined);
            console.log("Eliminando materia: ", idMat);
        },
        /* GENERACION */
        canAddSeccion(seccion, horTemp) {
            if (seccion) {
                //Existe'()
                for (let d = 0; d < 6; d++) {
                    //Mientras halla alguna clase ese dia, se recorre las horas para ver colisiones
                    if (Object.keys(seccion[d]).length > 0) {
                        for (let i = 7; i <= 21; i++) {
                            if (seccion[d][i]) {
                                //Hay algo a esa hora => chequemos en horario tmp
                                if (horTemp[d][i]) {
                                    //Chocamos
                                    //console.log("Chocamos dia: y hora: ", d, i);
                                    //console.log(seccion, horTemp);
                                    return 0;
                                }
                            }
                        }
                    }
                }
            } else {
                return null;
            }
            return 1;
        },
        addSeccion(seccion, horTemp) {
            if (seccion /*&& canAddSeccion(seccion, horTemp)*/) {
                let horAux = JSON.parse(JSON.stringify(horTemp));
                for (let d = 0; d < 6; d++) {
                    //Cuando hay clases ese dia => Copiamos
                    if (Object.keys(seccion[d]).length > 0) {
                        //Object.assign(horTemp[d-1], seccion[d]);
                        //Hacemos copia
                        Object.assign(horAux[d], seccion[d]);
                    }
                }
                return horAux;
            }
            return null;
        },
        publicar(hor) {
            //console.log("publicars")

            console.warn("PUBLICACION", hor);
            this.horariosGen.push(hor);
        },
        horario(idMat, idSeccion, horarioT) {
            //console.log("ahor", horarioT);
            //console.log("String", JSON.stringify(horarioT));
            //JSON.parse(JSON.stringify(horarioT));
            var horarioTemp = JSON.parse(JSON.stringify(horarioT));
            //console.log("dhor", horarioTemp);

            //Si existe materia
            if (this.matInscriptas[idMat]) {
                //console.log("Mat existe", idMat);
                //Mientras la seccion exista y idSecc no se pase
                while (idSeccion < this.matInscriptas[idMat].secciones.length) {
                    //console.log("ANTES horarioTemp", horarioTemp);
                    if (
                        this.canAddSeccion(
                            this.matInscriptas[idMat].secciones[idSeccion],
                            horarioTemp
                        )
                    ) {
                        //addSeccion(matInscriptas[idMat].secciones[idSeccion], horarioTemp);
                        //console.log("Seccion add", idSeccion);
                        //console.log("DESPUES horarioTemp", horarioTemp);
                        //console.log("Horario (",idMat + 1, 0);

                        this.horario(
                            idMat + 1,
                            0,

                            this.addSeccion(
                                this.matInscriptas[idMat].secciones[idSeccion],
                                horarioTemp
                            )
                        );
                    }
                    console.log("volvemos", idMat, idSeccion);
                    if (idSeccion == this.matInscriptas[idMat].secciones.length - 1 && idMat == 0) {
                        //llegamos a la ultima interacion
                        //Desactivar aviso "procesando"

                        this.msjGen = false;
                        //Cantidad de horarios en Array this.horariosGen
                        this.$gtag.event("HorariosGenerados", {
                            event_category: `HplusInteraccion`,
                            event_label: this.horariosGen.length,
                        });
                        if (!this.anyPublicado) {
                            //aviso de combinacciones no posibles
                            console.error("No hay combis");
                            this.showAlert = true;
                        }
                    }
                    idSeccion += 1;
                }

                //Comprobar si es vacio
                if (idMat == 0 && idSeccion == 0) {
                    let sumKey = 0;
                    horarioT.forEach(e => {
                        sumKey += Object.keys(e).length;
                    });
                    console.error(sumKey);
                    if (sumKey == 0) this.msjGen = false;
                }
            } else {
                //console.warn("Publicamos(", idMat, idSeccion);
                //No existe = no hay mas => publicar
                this.anyPublicado = true;
                this.publicar(horarioTemp);
            }
        },
        run() {
            let horarioGen = [
                //lunes 0
                {},
                //martes 1
                {},
                //miercoles 2
                {},
                //jueves 3
                {},
                //viernes
                {},
                //sabado
                {},
            ];
            console.warn("Comenzando a combinar!");

            this.showAlert = false;
            this.msjGen = true;

            this.horariosGen = [];
            this.matInscriptas = this.matInscriptasOriginal.filter(Boolean);
            this.horario(0, 0, horarioGen);
        },
        savePDF(idHor, mode) {
            console.log("Descargar horario-", idHor);

            this.$gtag.event("Download", {
                event_category: `HplusInteraccion`,
                event_label: mode,
            });

            let element = document.getElementById(`hor-${idHor}`);
            html2canvas(element, {
                allowTaint: true,
                scrollX: 0,
                scrollY: -window.scrollY, //LOS BENDITOS BOTONES >:(
                scale: window.devicePixelRatio * 1.5,
            }).then(canvas => {
                var imgData = canvas.toDataURL("image/jpg");

                if (mode == "image") {
                    let enlace = document.createElement("a");
                    enlace.download = `horario_${idHor + 1}.jpg`;
                    // Convertir la imagen a Base64
                    enlace.href = imgData;
                    // Hacer click en él
                    enlace.click();
                } else if (mode == "pdf") {
                    let doc = new jsPDF();

                    let width = doc.internal.pageSize.getWidth();
                    let height = doc.internal.pageSize.getHeight();

                    doc.addImage(imgData, "PNG", 1, 1, width, height);
                    doc.save(`horario_${idHor + 1}.pdf`);
                }
            });
        },
        recuperarSecciones(data) {
            //console.warn("Recibiendo: ", data);
            this.matInscriptasOriginal = data;
        },
    },
};
</script>

<style scoped>
.btn-action {
    white-space: normal !important;
}
</style>
