<template>
    <div>
        <div>
            <p
                v-show="matInscriptas.length <= 0"
                class="text-center accent--text text--lighten-1 mt-6"
            >
                ¡Añade materias y secciones!
            </p>
        </div>
        <v-row justify="center" v-show="matInscriptas.length > 0">
            <v-col sm="12" md="8">
                <v-expansion-panels accordion focusable hover>
                    <v-expansion-panel v-for="materia in filtrarMaterias" :key="materia.id">
                        <v-expansion-panel-header>
                            <v-col cols="2">
                                <v-btn icon tile @click="eliminarMateria(materia.id)">
                                    <v-icon color="error">mdi-close-circle-outline</v-icon>
                                </v-btn></v-col
                            >

                            <v-col cols="10">
                                <span>
                                    {{ materia.materia }}
                                </span>
                            </v-col>

                            <v-spacer></v-spacer>

                            <v-icon
                                :color="
                                    matInscriptas[materia.id].color
                                        ? matInscriptas[materia.id].color
                                        : 'white'
                                "
                                >mdi-circle</v-icon
                            >
                        </v-expansion-panel-header>
                        <v-expansion-panel-content>
                            <v-list>
                                <v-list-item
                                    v-for="(seccion, i) in matInscriptas[materia.id].secciones"
                                    :key="`sec-${i}`"
                                    v-show="seccion"
                                >
                                    <v-list-item-content>
                                        <v-list-item-title>{{
                                            matInscriptas[materia.id].nrc[i]
                                        }}</v-list-item-title>
                                    </v-list-item-content>
                                    <v-list-item-action>
                                        <v-btn icon @click="eliminarSeccion(i, materia.id)">
                                            <v-icon color="error">mdi-delete</v-icon>
                                        </v-btn>
                                    </v-list-item-action>
                                </v-list-item>
                            </v-list>

                            <v-btn
                                outlined
                                block
                                color="accent"
                                small
                                @click="openModalSeccion(materia.id)"
                                >+SECCIÓN</v-btn
                            >
                        </v-expansion-panel-content>
                    </v-expansion-panel>
                </v-expansion-panels>
            </v-col>
        </v-row>

        <!--MODAL SECCIONES-->
        <v-dialog
            v-model="dialog_seccion"
            fullscreen
            hide-overlay
            transition="dialog-bottom-transition"
        >
            <v-card>
                <v-toolbar dark color="primary">
                    <v-btn icon dark @click="dialog_seccion = false">
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                    <v-toolbar-title>Sección</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-toolbar-items>
                        <v-btn dark text @click="guardarSeccion">GUARDAR</v-btn>
                    </v-toolbar-items>
                </v-toolbar>

                <!-- CONTENIDO -->
                <div>
                    <v-form ref="formulario" v-model="formValido" lazy-validation>
                        <v-container>
                            <v-row>
                                <!--ALIAS-->
                                <v-col cols="12"
                                    ><v-text-field
                                        label="NRC o Alias"
                                        v-model="seccion.nrc"
                                        :rules="nonEmptyRule"
                                    ></v-text-field
                                ></v-col>

                                <!--- COLOR -->
                                <v-col cols="12" v-if="choseColor">
                                    <label>Color</label>
                                    <v-color-picker
                                        v-model="seccion.color"
                                        flat
                                        hide-inputs
                                        disabled
                                        hide-canvas
                                        show-swatches
                                        swatches-max-height="200"
                                        width="100%"
                                        :swatches="colores"
                                    ></v-color-picker>
                                </v-col>

                                <!-- Horario -->
                                <v-col cols="12">
                                    <label class="mb-2.">Horario de consulta</label>
                                    <v-subheader class="red--text"
                                        >{{ verificarHorariosLlenado() }}
                                    </v-subheader>
                                    <v-expansion-panels accordion multiple>
                                        <v-expansion-panel v-for="n in 6" :key="`dia-${n - 1}`">
                                            <v-expansion-panel-header>{{
                                                getDiaSemana(n - 1)
                                            }}</v-expansion-panel-header>
                                            <v-expansion-panel-content>
                                                <v-row
                                                    v-for="(horas, h) in seccion.horario[n - 1]"
                                                    :key="`hora-${n - 1}-${h}`"
                                                >
                                                    <v-col class="mt-1" cols="2">
                                                        <v-btn
                                                            fab
                                                            x-small
                                                            outlined
                                                            color="red"
                                                            @click="eliminarHorarioDia(n - 1, h)"
                                                            ><v-icon
                                                                >mdi-delete-forever</v-icon
                                                            ></v-btn
                                                        >
                                                    </v-col>
                                                    <v-col class="d-flex" cols="5">
                                                        <v-select
                                                            :items="opHora"
                                                            label="Inicio"
                                                            dense
                                                            v-model="seccion.horario[n - 1][h][0]"
                                                        ></v-select>
                                                    </v-col>
                                                    <v-col class="d-flex" cols="5">
                                                        <v-select
                                                            :items="opHora"
                                                            label="Fin"
                                                            dense
                                                            v-model="seccion.horario[n - 1][h][1]"
                                                        ></v-select>
                                                    </v-col>
                                                </v-row>
                                                <v-btn
                                                    block
                                                    small
                                                    outlined
                                                    color="green"
                                                    @click="agregarHorarioDia(n - 1)"
                                                    ><v-icon>mdi-plus</v-icon></v-btn
                                                >
                                            </v-expansion-panel-content>
                                        </v-expansion-panel>
                                    </v-expansion-panels>
                                </v-col>
                            </v-row>
                        </v-container>
                    </v-form>
                </div>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
export default {
    props: ["matInscriptas"],
    data: () => ({
        dialog_seccion: false,
        errorMSJ: "",
        choseColor: true,
        seccion: { id: "", nrc: "", color: "", horario: [[], [], [], [], [], []] },
        colores: [
            ["#F44336", "#E53935", "#D32F2F", "#C62828", "#B71C1C"],
            ["#E91E63", "#D81B60", "#C2185b", "#AD1457", "#880E4f"],
            ["#9C27B0", "#8E24Aa", "#7B1FA2", "#6A1B9a", "#4A148c"],
            ["#3F51B5", "#3949Ab", "#303F9f", "#283593", "#1A237e"],
            ["#2196Ff", "#1E88E5", "#1976D2", "#1565C0", "#0D47A1"],
            ["#00BCD4", "#00ACC1", "#0097A7", "#00838f", "#006064"],
            ["#009688", "#00897b", "#00796b", "#00695c", "#004D40"],
            ["#4CAF50", "#43A047", "#388E3c", "#2E7D32", "#1B5E20"],
            ["#FF9800", "#FB8C00", "#F57C00", "#EF6C00", "#E65100"],
            ["#FF5722", "#F4511e", "#E64A19", "#D84314", "#BF360c"],
            ["#795548", "#6D4C41", "#5D4037", "#4E342e", "#3E2723"],
            ["#607D8b", "#546E7a", "#455A64", "#37474f", "#263238"],
        ],
        opHora: [
            { text: "7:00 AM", value: 7 },
            { text: "8:00 AM", value: 8 },
            { text: "9:00 AM", value: 9 },
            { text: "10:00 AM", value: 10 },
            { text: "11:00 AM", value: 11 },
            { text: "12:00 PM", value: 12 },
            { text: "1:00 PM", value: 13 },
            { text: "2:00 PM", value: 14 },
            { text: "3:00 PM", value: 15 },
            { text: "4:00 PM", value: 16 },
            { text: "5:00 PM", value: 17 },
            { text: "6:00 PM", value: 18 },
            { text: "7:00 PM", value: 19 },
            { text: "8:00 PM", value: 20 },
            { text: "9:00 PM", value: 21 },
            { text: "10:00 PM", value: 22 },
        ],
        nonEmptyRule: [v => !!v || "Es requerido"],
        formValido: false,
    }),

    methods: {
        openModalSeccion(idMat) {
            this.dialog_seccion = true;
            this.seccion.id = idMat;
            this.choseColor = this.matInscriptas[idMat].color == "" ? true : false;
        },
        getDiaSemana(num) {
            let semana = ["Lunes", "Martes", "Miercoles", "Jueves", "Viernes", "Sabado"];
            return semana[num];
        },
        eliminarHorarioDia(dia, nhor) {
            this.seccion.horario[dia].splice(nhor, 1);
        },
        agregarHorarioDia(diaTo) {
            if (this.seccion.horario[diaTo][this.seccion.horario[diaTo].length - 1] != "")
                this.seccion.horario[diaTo].push([]);
        },
        verificarHorariosLlenado() {
            let errorMSJ = "*Debe registrar al menos 1 horario!";
            for (let i = 0; i < this.seccion.horario.length; i++) {
                for (let j = 0; j < this.seccion.horario[i].length; j++) {
                    if (this.seccion.horario[i][j][0] && this.seccion.horario[i][j][1]) {
                        errorMSJ = null;
                        if (!(this.seccion.horario[i][j][1] - this.seccion.horario[i][j][0] > 0)) {
                            //Diff < 0
                            errorMSJ = "*La hora final debe ser superior a la inicial!";
                        }
                    }
                }
            }
            return errorMSJ;
        },
        guardarSeccion() {
            if (this.$refs.formulario.validate() && this.verificarHorariosLlenado() == null) {
                console.log("guardando...");
                let horarioObj = [{}, {}, {}, {}, {}, {}];

                //Conversion Array Par a Obj Horarios
                this.seccion.horario.forEach((dia, i) => {
                    dia.forEach(clase => {
                        let dif = clase[1] - clase[0];
                        for (let h = 0; h < dif; h++) {
                            horarioObj[i][clase[0] + h] = [
                                this.seccion.id,
                                this.matInscriptas[this.seccion.id].secciones.length,
                            ]; //MatID, SecID
                        }
                    });
                });

                //EMITIR EVENTO PARA SER CAPTURADO EN APP
                this.$emit("guardarSeccion", {
                    idMat: this.seccion.id,
                    nrc: this.seccion.nrc,
                    color: this.seccion.color,
                    horario: horarioObj,
                });
                //console.warn("Objecto ", horarioObj);

                //Vaciamos
                this.seccion = { nrc: "", color: "", horario: [[], [], [], [], [], []] };
                this.dialog_seccion = false;
            }
        },
        eliminarSeccion(idSec, idMat) {
            this.$emit("eliminarSeccion", idSec, idMat);
        },
        eliminarMateria(idMat) {
            this.$emit("eliminarMateria", idMat);
        },
    },

    computed: {
        filtrarMaterias() {
            return this.matInscriptas.filter(Boolean);
        },
    },
};
</script>
