<template>
    <v-dialog v-model="dialog_materias" fullscreen transition="scroll-y-reverse-transition">
        <template v-slot:activator="{ on }">
            <v-btn
                v-on="on"
                rounded
                block
                color="primary"
                class="font-weight-light title"
                :disabled="disableSelect"
                >MATERIAS</v-btn
            >
        </template>

        <!-- CARD/MODAL -->
        <v-card>
            <v-toolbar dark color="primary">
                <v-btn icon dark @click="dialog_materias = false">
                    <v-icon>mdi-close</v-icon>
                </v-btn>
                <v-toolbar-title>MATERIAS</v-toolbar-title>
            </v-toolbar>

            <!-- CONTENIDO -->
            <div>
                <v-expansion-panels accordion focusable hover>
                    <v-expansion-panel v-for="semestre in agruparSem" :key="semestre.semestre">
                        <v-expansion-panel-header>{{ semestre.semestre }}</v-expansion-panel-header>
                        <v-expansion-panel-content>
                            <v-list rounded>
                                <v-list-item
                                    color="primary"
                                    dense
                                    :input-value="matInscriptas[materia.id]"
                                    v-for="materia in semestre.materias"
                                    :key="materia.id"
                                    @click="materiaSelecionada(materia.id)"
                                >
                                    <v-list-item-content>
                                        <v-list-item-title>
                                            {{ materia.asignatura }}
                                        </v-list-item-title>
                                    </v-list-item-content>
                                </v-list-item>
                            </v-list>
                        </v-expansion-panel-content>
                    </v-expansion-panel>
                </v-expansion-panels>
            </div>
        </v-card>
    </v-dialog>
</template>

<script>
export default {
    props: ["materias", "open", "matInscriptas"],
    data: () => ({
        dialog_materias: false,
        disableSelect: true,
        matInscriptasControl: [],
    }),
    methods: {
        materiaSelecionada(id) {
            //console.log("Materia ID: ", id);
            this.$emit("materiaSelecionada", id);
        },
    },
    computed: {
        agruparSem() {
            let lastSem = "";
            let semAct = "";

            let iSem = -1;
            //[{ semestre: "", materias: [] }]
            let semestres = [];

            for (let i = 0; i < this.materias.length; i++) {
                semAct = this.materias[i].Semestre;

                //nuevo semestre
                if (semAct != lastSem) {
                    lastSem = semAct;
                    iSem++;

                    semestres.push({
                        semestre: semAct,
                        materias: [],
                    });
                }
                //nueva materia
                semestres[iSem].materias.push({
                    asignatura: this.materias[i].Asignatura,
                    id: i,
                });
            }
            return semestres;
        },
    },
    watch: {
        materias(newVal) {
            if (newVal) {
                this.disableSelect = false;
            } else {
                this.disableSelect = true;
            }
        },
        open(newVal) {
            this.dialog_materias = newVal ? true : false;
        },
    },
};
</script>
