<template>
    <v-row justify="center">
        <v-col sm="6" md="3">
            <v-btn block outlined small @click="guardarActual">Guardar Horario</v-btn>
        </v-col>
        <v-col sm="6" md="3">
            <v-btn block outlined small @click="cargarUltimo">Cargar último</v-btn>
        </v-col>
    </v-row>
</template>

<script>
export default {
    props: ["materiasInscriptas"],
    methods: {
        guardarActual() {
            //JSON.stringify
            localStorage.setItem("horario", JSON.stringify(this.materiasInscriptas));
            this.$gtag.event("GuardarActual", {
                event_category: `HplusInteraccion`,
            });
        },
        cargarUltimo() {
            let data = JSON.parse(localStorage.getItem("horario"));
            this.$gtag.event("RecuperarHorario", {
                event_category: `HplusInteraccion`,
            });
            if (data) this.$emit("dataUltimo", data);
        },
    },
};
</script>
